import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/HomeView.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/download",
    component: () => import("../views/DownloadPage.vue"),
  },
  {
    path: '/NuncaTe',
    name: 'NuncaTe',
    component: () => import('../views/NuncaTe.vue'),
  },
  {
    path: '/ultimo',
    name: 'ultimo',
    component: () => import('../views/OnlineRead/UltimoVinculo.vue'),
  },
  {
    path: '/common',
    name: 'common',
    component: () => import('../views/commonView.vue'),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
